import React from "react";
import { Container, Row, Col } from "react-bootstrap";
import ProjectCard from "./ProjectCards";
import Particle from "../Particle";
import iris from "../../Assets/Projects/iris.jpg";
import covid from "../../Assets/Projects/covid.jpg";
import rec from "../../Assets/Projects/rec.png";
import inventory from "../../Assets/Projects/inventory.png";


function Projects() {
  return (
    <Container fluid className="project-section">
      <Particle />
      <Container>
        <h1 className="project-heading">
          My Recent <strong className="purple">Works </strong>
        </h1>
        <p style={{ color: "white" }}>
          Here are a few projects I've worked on recently.
        </p>
        <Row style={{ justifyContent: "center", paddingBottom: "10px" }}>
          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={iris}
              isBlog={false}
              title="Iris Flower Classification"
              description="Iris Flower Classification By Using Machine Learning Dission Tree Algorithm."
              link="https://github.com/pramodpm2"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={covid}
              isBlog={false}
              title="Covid19 Cases Latest Updates"
              description="I Have Written Some Simple Python Scripts To Get Latest Covid19 Cases Details By Country Name."
              link="https://github.com/pramodpm2"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={rec}
              isBlog={false}
              title="Campus Recruitment System"
              description="I have developed Campus Recruitment System using Python Django and MySQL The main modules available in this project are Placement module which manages the functionality of Placement is normally used for managing Placement, Qualification contains all the functionality realted to Qualification."
              link="https://github.com/pramodpm2"
            />
          </Col>

          <Col md={4} className="project-card">
            <ProjectCard
              imgPath={inventory}
              isBlog={false}
              title="Inventory Management System"
              description=" This Inventory Management System gives you the ability to check on your products, add customers, manage suppliers, manage category, manage order and you can also modify them and more."
              link="https://github.com/pramodpm2"
            />
          </Col>

        </Row>
      </Container>
    </Container>
  );
}

export default Projects;
