import React, { useState, useEffect } from "react";
import { Container, Row, Col } from "react-bootstrap";
import Button from "react-bootstrap/Button";
import Particle from "../Particle";
import Resumecontent from "./ResumeContent";
import axios from "axios";
import pdf from "../../Assets/Resume.pdf";
import { AiOutlineDownload } from "react-icons/ai";

function Resume() {
  const uri = "https://porfolio-backend.vercel.app/ranks/getRanks";
  const [spojRank, upadteSpojRank] = useState(0);
  const [hackerrank, upadteHackerank] = useState(0);
  const [sem, upadateSem] = useState(0);
  const [cgpa, upadteCgpa] = useState(0);

  useEffect(() => {
    axios
      .get(uri)
      .then((res) => {
        upadteSpojRank(res.data.message[0].spojRank);
        upadteHackerank(res.data.message[1].hackerrank);
        upadteCgpa(res.data.message[2].cgpa);
        upadateSem(res.data.message[3].sem);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  return (
    <Container fluid className="resume-section">
      <Particle />
      <Container>
        <Row style={{ justifyContent: "center", position: "relative" }}>
          <Button variant="primary" href={pdf} target="_blank">
            <AiOutlineDownload />
            &nbsp;Download CV
          </Button>
        </Row>
        <Row className="resume">
          <Col md={6} className="resume-left">
            <h3 className="resume-title">Experience</h3>
            <Resumecontent
              title="Full Stack Developer Intern At Settyl Inc"
              date="April 2021 - on Going"
              content={[
                "Developing The SupplyChain Managemant Software Product, I am Handling Three Modules In This Software.",
              ]}
            />
            <h3 className="resume-title">Extracurricular Activities</h3>
            <Resumecontent
              title="Web Developer [ Technofia-2021 Technical Fest of AIET ]"
              content={[
                "Worked on creating the frontend-end of the website using React Js, Material UI.",
              ]}
            />
            <Resumecontent
              title="Web Developer [ Wartex-2020 Technical Fest of AIET ]"
              content={[
                "Operated on developing the frontend end of the website using Bootstrap, HTML&CSS backend with Django and Got Second Place",
                
              ]}
            />
          </Col>
          <Col md={6} className="resume-right">
            <h3 className="resume-title">Education</h3>
            <Resumecontent
              title="Computer Science And Engineering [  Alva's Institute Of Engineering And Technology, Moodbidri, Mangalore  ] "
              date="2018 - Present"
              content={[`CGPA: 8.4 (Till ${5}th Sem)`]}
            />
            <Resumecontent
              title="12TH STATE BOARD [ SCM PU College, Shikaripura, Karnataka]"
              date="2016 - 2018"
              content={["Precentage: 81.33%"]}
            />
            <Resumecontent
              title="SSLC [  Govt High School, Baganakatte ,Shikaripura  ]"
              date="2013 - 2016"
              content={["Precentage: 88.16%"]}
            />
          
           
          </Col>
        </Row>
        <Row style={{ justifyContent: "center", position: "relative" }}>
          <Button variant="primary" href={pdf} target="_blank">
          <AiOutlineDownload />&nbsp;Download CV
          </Button>
        </Row>
      </Container>
    </Container>
  );
}

export default Resume;
