import React from "react";
import Card from "react-bootstrap/Card";
import { ImPointRight } from "react-icons/im";

function AboutCard() {
  return (
    <Card className="quote-card-view">
      <Card.Body>
        <blockquote className="blockquote mb-0">
          <p style={{ textAlign: "justify" }}>
            Hi Everyone, I am <span className="purple">Pramod P M </span>
            from <span className="purple"> Shikaripura, Karnataka, India.</span>
            <br />I am student pursuing B.E in Computer Science And Engineering in Alva's Institute Of Engineering And Technology 
            <br />
            <br />
            Apart from coding, some other activities that I love to do!
          </p>
          <ul>
            <li className="about-activity">
              <ImPointRight /> Cooking
            </li>
            <li className="about-activity">
              <ImPointRight /> Travelling
            </li>
          </ul>

          <p style={{ marginBlockEnd: 0, color: "rgb(155 126 172)" }}>
            "Experience is the name everyone gives to their mistakes !"{" "}
          </p>
          <footer className="blockquote-footer">Pramod P M</footer>
        </blockquote>
      </Card.Body>
    </Card>
  );
}

export default AboutCard;
